@import (reference) '../../../less/id7.less';

.id7-main-content-area {
  .row-spacer {
    > [class*='col-'] {
      padding-top: @grid-gutter-width;
      padding-bottom: @grid-gutter-width;
    }
  }

  .slideshow {
    background: black;
    color: white;
    margin-bottom: @grid-gutter-width;

    .carousel {
      height: 400px;

      .carousel-inner, .carousel-inner > .item {
        height: 100%;
      }
    }

    .carousel-inner > .item {
      > .caption {
        h1, h2, h3, h4, h5, h6,
        .h1, .h2, .h3, .h4, .h5, .h6 {
          color: inherit;
          border: 0;
        }

        width: 290px;
        max-width: 100%;
        position: absolute;
        top: @grid-gutter-width;
        right: @grid-gutter-width;
        bottom: (@grid-gutter-width * 1.5);
        padding: @grid-gutter-width;
        background-color: #555555;
        background-color: rgba(0, 0, 0, 0.8);
        overflow: auto;
      }
    }

    .carousel-controls {
      height: @grid-gutter-width;
      line-height: @grid-gutter-width;
      position: absolute;
      bottom: 0px;
      left: 0px;

      .link-colour(white);
      background-color: #555555;
      background-color: rgba(0, 0, 0, 0.8);
    }
  }

  #boxstyle > .filled(~'.box1', screen(@id7-brand-purple, darken(white, 40%)));
  #boxstyle > .filled(~'.box2', screen(@id7-brand-purple, darken(white, 10%)));
  #boxstyle > .filled(~'.box3', #e6e6e6);
  #boxstyle > .image(~'.box4', ~'url(../site/site1/banner-image.jpg) top right no-repeat', white);
  #boxstyle > .image(~'.box5', ~'url(../site/site3/banner-image.jpg) top right no-repeat', white);

  .feature-box {
    .heading {
      h1, h2, h3, h4, h5, h6,
      .h1, .h2, .h3, .h4, .h5, .h6 {
        color: inherit;
        border: 0;
        margin: 0;
        padding: 0;
      }

      display: inline-block;
      margin-left: (-(@grid-gutter-width * 3/4));
      padding: (@grid-gutter-width / 2) @grid-gutter-width;
    }

    position: relative;

    .caption {
      position: absolute;
      bottom: 0px;
      left: 0px;
      width: 100%;
      padding: @grid-gutter-width;

      #gradient > .vertical(@start-color: rgba(0,0,0,.0001); @end-color: rgba(0,0,0,.5));

      > :last-child {
        margin-bottom: 0;
      }
    }
  }

  .feature-image1 {
    color: white;
    min-height: 200px;
    font-weight: bold;

    .heading {
      background: #6b3251;
      color: contrast(#6b3251, @text-color, white, 50%);
    }
  }

  .feature-image2 {
    color: white;
    min-height: 200px;
    font-weight: bold;

    .heading {
      background-color: #555555;
      background-color: rgba(0, 0, 0, 0.8);
      color: white;
    }
  }

  .btn-block {
    font-size: 160%;
    text-align: left;
    padding: 0;
    min-height: 60px;

    position: relative;

    > span {
      position: absolute;
      left: @grid-gutter-width;
      bottom: @grid-gutter-width;
    }
  }

  .btn-colour1 {
    background: #1f2121;
    color: #dbc6d7;

    &:hover,
    &:active {
      background: darken(#1f2121, 10%);
      color: lighten(#dbc6d7, 10%);
    }
  }

  .btn-colour2 {
    background: #6b3251;
    color: #dbc6d7;

    &:hover,
    &:active {
      background: darken(#6b3251, 10%);
      color: lighten(#dbc6d7, 10%);
    }
  }

  blockquote.no-margin {
    margin: 0;
  }

  .nav-flex {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    width: 100%;

    > li {
      float: none;
      margin: 0 !important;

      @media (min-width: @screen-sm-min) {
        width: floor((@container-sm - @grid-gutter-width) / 13);
      }

      @media (min-width: @screen-md-min) {
        width: floor((@container-md - @grid-gutter-width) / 26);
      }

      @media (min-width: @screen-lg-min) {
        width: floor((@container-lg - @grid-gutter-width) / 26);
      }

      > a {
        text-align: center;
        margin-bottom: 5px;
      }
    }

    > .dropdown .dropdown-menu {
      top: auto;
      left: auto;
    }
  }
}

ul.branded {
  > li {
    font-weight: bold;

    &:nth-child(7n + 1):before { color: @id7-brand-purple; }
    &:nth-child(7n + 2):before { color: @id7-brand-gold; }
    &:nth-child(7n + 3):before { color: @id7-brand-red; }
    &:nth-child(7n + 4):before { color: @id7-brand-green; }
    &:nth-child(7n + 5):before { color: @id7-brand-blue; }
    &:nth-child(7n + 6):before { color: @id7-brand-orange; }
    &:nth-child(7n + 7):before { color: @id7-brand-gray; }
  }

  list-style: none;

  li:before {
    .fa();
    font-size: 12px;

    content: @fa-var-play;
    position: absolute;
    margin-left: -18px;
    margin-top: 1px;

    .opacity(0.9);

    line-height: floor((ceil((@main-font-size * @font-scale-xs)) * @line-height-base));

    @media (min-width: @grid-float-breakpoint) {
      line-height: floor((@main-font-size * @line-height-base));
    }
  }

  ul li:before {
    .opacity(0.8);
  }

  ul ul li:before {
    font-size: (0.8 * 12px);
    .opacity(0.7);
  }

  ul ul ul li:before {
    .opacity(0.6);
  }
}

blockquote.branded {
  border-left: none;
  padding: (@line-height-computed / 2);

  color: @id7-brand-default;
  border-top: 1px solid @id7-brand-default;
  background: url(/dist/images/logo-divider-purple.svg) right bottom no-repeat;
  background-size: 1130px 41px;

  footer {
    color: inherit;
    &:before { content: none; }

    cite {
      font-style: normal;
    }
  }

  padding-bottom: @grid-gutter-width;
  position: relative;
  footer {
    position: absolute;
    left: 0px;
    height: @grid-gutter-width;
    overflow: hidden;
    font-size: 75%;
    line-height: @line-height-small;
    padding: 3px 110px 3px 0;
  }
}